@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* fonts */
/* variables custom css */
:root{
  --bg-black-900: #000000;
  --bg-black-100: #dddddd;
  --bg-black-50: #eff0f4;
  --text-black-900: #000000;
  --text-black-700: #555555;
  --text-black-600: #666666;
  --text-black-300: #bbbbbb;
  --outer-shadow: 3px 3px 3px #d0d0d0, -3px -3px 3px #f8f8f8;
  --outer-shadow-0: 0 0 0 #d0d0d0, 0 0 0 #f8f8f8;
  --inner-shadow: inset 3px 3px 3px #d0d0d0, inset -3px -3px 3px #f8f8f8;
}

/* global style */
body{
  font-size: 16px;
  line-height: 1.5;
  overflow-x: hidden; 
  background-color: #eff0f4; 
  background-color: var(--bg-black-50);
}

/* *:not(i){
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
} */

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}

::before,::after {
box-sizing: border-box;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
  margin: 0;
  padding: 0;
}

img{
  vertical-align: middle;
  max-width: 100%;
}

.container{
  max-width: 1140px;
  margin: auto;
}

.row{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.justify-content-between{
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.outer-shadow{
  box-shadow: 3px 3px 3px #d0d0d0, -3px -3px 3px #f8f8f8;
  box-shadow: var(--outer-shadow);
}

.inner-shadow{
  box-shadow: inset 3px 3px 3px #d0d0d0, inset -3px -3px 3px #f8f8f8;
  box-shadow: var(--inner-shadow);
}

.houver-in-shadow{
  position: relative;
  z-index: 1;
}

.houver-in-shadow:hover{
  box-shadow: 0 0 0 #d0d0d0, 0 0 0 #f8f8f8;
  box-shadow: var(--outer-shadow-0);
}

.houver-in-shadow:after{
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transition: all 0.3s ease;
}

.houver-in-shadow:hover:after{
  box-shadow: inset 3px 3px 3px #d0d0d0, inset -3px -3px 3px #f8f8f8;
  box-shadow: var(--inner-shadow);
}

.align-items-center{
  -webkit-align-items: center;
          align-items: center;
}

.btn-1{
  padding: 10px 25px;
  font-size: 16px;
  font-weight: 500;
  color: #fb839e;
  background-color: transparent;
  line-height: 1.5;
  cursor: pointer;
  border-radius: 30px;
  transition: all 0.3s ease;
  display: inline-block;
}

.btn-1::after{
  border-radius: 30px;
}

.effect-wrap .effect{
  position: absolute;
  z-index: -1;
}

.effect-1{
  width: 30px;
  height: 30px;
  border: 4px solid #8a49ff;
  right: 10%;
  bottom: 10%;
  -webkit-animation: spin 10s linear infinite;
          animation: spin 10s linear infinite;
}

.effect-2{
  left: 3%;
  bottom: 20%;
  width: 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-animation: topBounce 3s ease-in-out infinite;
          animation: topBounce 3s ease-in-out infinite;
}

.effect-2 div{
  height: 3px;
  width: 3px;
  background-color: #ff9c07;
  margin: 0 3px 8px;
}
.effect-3{
  height: 180px;
  width: 180px;
  border: 25px solid #fb839e;
  border-radius: 50%;
  left: 50%;
  top: -120px;
  -webkit-animation: leftBounce 3s ease-in-out infinite;
          animation: leftBounce 3s ease-in-out infinite;
}

@-webkit-keyframes spin{
  0%{
    trasnform: rotate(0deg);
  }
  100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin{
  0%{
    trasnform: rotate(0deg);
  }
  100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes topBounce{
  0%, 100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50%{
    -webkit-transform: translateY(25px);
            transform: translateY(25px);
  }
}

@keyframes topBounce{
  0%, 100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50%{
    -webkit-transform: translateY(25px);
            transform: translateY(25px);
  }
}

@-webkit-keyframes leftBounce{
  0%, 100%{
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  50%{
    -webkit-transform: translateX(25px);
            transform: translateX(25px);
  }
}

@keyframes leftBounce{
  0%, 100%{
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  50%{
    -webkit-transform: translateX(25px);
            transform: translateX(25px);
  }
}

/* header   */
.header {
  padding: 20px 15px;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 99;
}

.header .logo a{
  display: inline-block;
  text-align: center;
  line-height: 40px;
  height: 40px;
  width: 40px;
  font-size: 24px;
  color: #fb839e;
  border-radius: 50%;
  border: 2px solid #fb839e;
  font-weight: 600;
  text-transform: uppercase;
}

.header .hamburger-btn{
  height: 40px;
  width: 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
  border-radius: 50%;
  -webkit-justify-content: center;
          justify-content: center;
  transition: all 0.3s ease;
}

.header .hamburger-btn::after{
  border-radius: 50%;
}

.header .hamburger-btn span {
  display: block;
  height: 2px;
  width: 16px;
  background-color: #000000;
  background-color: var(--bg-black-900);
  position: relative;
}

.header .hamburger-btn span::before,
.header .hamburger-btn span::after{
  content: '';
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
  background-color: var(--bg-black-900);
}

.header .hamburger-btn span::before{
  top: -6px;
}

.header .hamburger-btn span::after{
  top: 6px;
}

/* navigation menu */
.nav-menu{
  position:fixed;
  background-color: #eff0f4;
  background-color: var(--bg-black-50);
  padding: 0 15px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  overflow-y: auto;
}

.nav-menu .close-nav-menu{
  height: 40px;
  width: 40px;
  display: block;
  font-size: 35px;
  line-height: 35px;
  border-radius: 50%;
  position: absolute;
  right: 15px;
  top: 15px;
  cursor: pointer;
  text-align: center;
  color: #666666;
  color: var(--text-black-600);
  transition: all 0.3s ease;
}

.nav-menu .close-nav-menu::after{
  border-radius: 50%;
}

.nav-menu-inner{
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.nav-menu ul{
  padding: 15px;
}

.nav-menu ul li{
  display: block;
  margin-bottom: 20px;
  text-align: center;
}

.nav-menu ul li:last-child{
  margin-bottom: 0;
}

.nav-menu ul li a{
  display: inline-block;
  font-size: 28px;
  font-weight: 600;
  color: #555555;
  color: var(--text-black-700);
  padding: 5px 30px;
  text-transform: capitalize;
  border-radius: 30px;
  transition: all 0.3s ease;
}

.nav-menu ul li a.active{
  color: #fb839e;
}

.nav-menu ul li a::after{
  border-radius: 30px;
}

.nav-menu .copyright-text{
  position: absolute;
  top: 50%;
  font-size: 15px;
  color: #666666;
  color: var(--text-black-600);
  -webkit-transform: translateY(-50%) rotate(-90deg);
          transform: translateY(-50%) rotate(-90deg);
  left: -100px;
}

/* home section */
.home-section{
  position: relative;
}

.home-section .full-screen{
  padding: 50px 0;
  min-height: 100vh;
}

.home-section .home-text,
.home-section .home-img{
  -webkit-flex: 0 0 50%;
          flex: 0 0 50%;
  padding: 0 15px;
}

.home-section .home-text p{
  font-family: 'Raleway', sans-serif;
  color: #bbbbbb;
  color: var(--text-black-300);
  font-size: 18px;
  text-transform: capitalize;
  margin: 0;
}

.home-section .home-text h2{
  font-family: 'Raleway', sans-serif;
  font-size: 55px;
  font-weight: 700;
  color: #000000;
  color: var(--text-black-900);
}

.home-section .home-text h1{
  font-family: 'Raleway', sans-serif;
  font-size: 20px;
  font-weight: 500;
  color: #555555;
  color: var(--text-black-700);
  margin: 0;
  text-transform: capitalize;
}

.home-section .home-text .btn-1{
  margin-top: 30px;
}

.home-section .home-img .img-box{
  max-width: 450px;
  display: block;
  margin: auto;
  border-radius: 50%;
  padding: 15px;
}

.home-section .home-img .img-box img{
  width: 100%;
  border-radius: 50%;
  border: 10px solid transparent;
}
